import {mapGetters} from "vuex";

export  default {
  computed:{
    ...mapGetters(["net", "account", "balance", "contract"]),
  },
  data() {
    return {
      logo: require('./assets/ethereum.svg'),
      popBox: false,
      visible: false,
      INVEST_MIN_AMOUNT: 0, //100 trx
      BASE_PERCENT: 0, //
      REFERRAL_PERCENTS: null, // 
      MARKETING_FEE: 0, // 
      PROJECT_FEE: 0, // 
      DAY_FEE: 0, // 
      PERCENTS_DIVIDER: 0, // 
      CONTRACT_BALANCE_STEP: 0, //
      TIME_STEP: 0, // 
      ADD_DEPOSITS_TIME_STEP: 0, // 
      totalUsers: 0, // 
      totalInvested: 0, // 
      totalWithdrawn: 0, // 
      totalDeposits: 0, // 
      totalTodayInvested: 0, // 
      bonusStart: 0, // 
      depBonusEnd: 0, // 
      totalDayMaxInvested: 0, // 24
      maxInvestedUser: 0, // 24
      bonusUsers: 0, // 10
      marketingAddress: null, // 
      projectAddress: null, // 
      contractBalance: 0, // 
      contractBalanceRate: 0, // +
      userPercentRate: 0, // + +
      userDividends: 0, // 
      userCheckpoint: 0, // 
      userReferrer: null, // 
      userReferralBonus: 0, // 
      userAvailable: 0, // +
      isActive: false, // 
      userAmountOfDeposits: 0, // 
      userTotalDeposits: 0,  // 
      userTotalWithdrawn: 0, // 
      userLastInvestInfo: null, // 
      investAmount:1,
      backImg: require('./assets/mainback.jpg'),
      popTitle:'',
      value1: 1,
      value2: 0,
      computeVisible:false,
      myInfoVisible:false,
      investVisible:false
    }
  },
  watch: {
    account: async function () {
      await this.setInterFace();
    },
    net: async function () {
      await this.setInterFace();
    }
  },
  methods:{
    async setInterFace() {
      let contractBalance = await this.contract.methods.getContractBalance().call(),
        totalInvested = await this.contract.methods.totalInvested().call(),
        INVEST_MIN_AMOUNT = await this.contract.methods.INVEST_MIN_AMOUNT().call(),
        CONTRACT_BALANCE_STEP = await this.contract.methods.CONTRACT_BALANCE_STEP().call(),
        totalWithdrawn = await this.contract.methods.totalWithdrawn().call(),
        totalTodayInvested = await this.contract.methods.totalTodayInvested().call(),
        totalDayMaxInvested = await this.contract.methods.totalDayMaxInvested().call(),
        userReferralBonus = await this.contract.methods.getUserReferralBonus(this.account).call(),
        userAvailable = await this.contract.methods.getUserAvailable(this.account).call(),
        userTotalDeposits = await this.contract.methods.getUserTotalDeposits(this.account).call(),
        userTotalWithdrawn = await this.contract.methods.getUserTotalWithdrawn(this.account).call(),

        BASE_PERCENT = parseFloat(await this.contract.methods.BASE_PERCENT().call()),
        REFERRAL_PERCENTS = [parseFloat(await this.contract.methods.REFERRAL_PERCENTS(0).call()),
          parseFloat(await this.contract.methods.REFERRAL_PERCENTS(1).call()),
          parseFloat(await this.contract.methods.REFERRAL_PERCENTS(2).call())],
        bonusUsers = [await this.contract.methods.bonusUsers(9).call(),
          await this.contract.methods.bonusUsers(8).call(),
          await this.contract.methods.bonusUsers(7).call(),
          await this.contract.methods.bonusUsers(6).call(),
          await this.contract.methods.bonusUsers(5).call(),
          await this.contract.methods.bonusUsers(4).call(),
          await this.contract.methods.bonusUsers(3).call(),
          await this.contract.methods.bonusUsers(2).call(),
          await this.contract.methods.bonusUsers(1).call(),
          await this.contract.methods.bonusUsers(0).call()],
        MARKETING_FEE = parseFloat(await this.contract.methods.MARKETING_FEE().call()),
        DAY_FEE = parseFloat(await this.contract.methods.DAY_FEE().call()),
        PERCENTS_DIVIDER = parseFloat(await this.contract.methods.PERCENTS_DIVIDER().call()),
        TIME_STEP = parseFloat(await this.contract.methods.TIME_STEP().call()),
        ADD_DEPOSITS_TIME_STEP = parseFloat(await this.contract.methods.ADD_DEPOSITS_TIME_STEP().call()),
        totalUsers = parseFloat(await this.contract.methods.totalUsers().call()),
        totalDeposits = parseFloat(await this.contract.methods.totalDeposits().call()),
        bonusStart = parseFloat(await this.contract.methods.bonusStart().call()),
        depBonusEnd = parseFloat(await this.contract.methods.depBonusEnd().call()),
        maxInvestedUser = parseFloat(await this.contract.methods.maxInvestedUser().call()),
        userDividends = parseFloat(await this.contract.methods.getUserDividends(this.account).call()),
        marketingAddress = await this.contract.methods.marketingAddress().call(),
        projectAddress = await this.contract.methods.projectAddress().call(),
        userReferrer = await this.contract.methods.getUserReferrer(this.account).call(),
        isActive = await this.contract.methods.isActive(this.account).call(),
        contractBalanceRate = parseFloat(await this.contract.methods.getContractBalanceRate().call()),
        userPercentRate = parseFloat(await this.contract.methods.getUserPercentRate(this.account).call()),
        userCheckpoint = parseFloat(await this.contract.methods.getUserCheckpoint(this.account).call()),
        userAmountOfDeposits = parseFloat(await this.contract.methods.getUserAmountOfDeposits(this.account).call()),
        PROJECT_FEE = parseFloat(await this.contract.methods.PROJECT_FEE().call());


      this.$set(this, "contractBalance", parseFloat(web3.utils.fromWei(contractBalance, 'ether')));
      this.$set(this, "totalInvested", parseFloat(web3.utils.fromWei(totalInvested, 'ether')));
      this.$set(this, "INVEST_MIN_AMOUNT", parseFloat(web3.utils.fromWei(INVEST_MIN_AMOUNT, 'ether')));
      this.$set(this, "CONTRACT_BALANCE_STEP", parseFloat(web3.utils.fromWei(CONTRACT_BALANCE_STEP, 'ether')));
      this.$set(this, "totalWithdrawn", parseFloat(web3.utils.fromWei(totalWithdrawn, 'ether')));
      this.$set(this, "totalTodayInvested", parseFloat(web3.utils.fromWei(totalTodayInvested, 'ether')));
      this.$set(this, "totalDayMaxInvested", parseFloat(web3.utils.fromWei(totalDayMaxInvested, 'ether')));
      this.$set(this, "userReferralBonus", parseFloat(web3.utils.fromWei(userReferralBonus, 'ether')));
      this.$set(this, "userAvailable", parseFloat(web3.utils.fromWei(userAvailable, 'ether')));
      this.$set(this, "userTotalDeposits", parseFloat(web3.utils.fromWei(userTotalDeposits, 'ether')));
      this.$set(this, "userTotalWithdrawn", parseFloat(web3.utils.fromWei(userTotalWithdrawn, 'ether')));
      this.$set(this, "BASE_PERCENT", BASE_PERCENT);
      this.$set(this, "REFERRAL_PERCENTS", REFERRAL_PERCENTS);
      this.$set(this, "MARKETING_FEE", MARKETING_FEE);
      this.$set(this, "PROJECT_FEE", PROJECT_FEE);
      this.$set(this, "DAY_FEE", DAY_FEE);
      this.$set(this, "PERCENTS_DIVIDER", PERCENTS_DIVIDER);
      this.$set(this, "TIME_STEP", TIME_STEP);
      this.$set(this, "ADD_DEPOSITS_TIME_STEP", ADD_DEPOSITS_TIME_STEP);
      this.$set(this, "totalUsers", totalUsers);
      this.$set(this, "totalDeposits", totalDeposits);
      this.$set(this, "bonusStart", bonusStart);
      this.$set(this, "depBonusEnd", depBonusEnd);
      this.$set(this, "contractBalanceRate", contractBalanceRate);
      this.$set(this, "userPercentRate", userPercentRate);
      this.$set(this, "userCheckpoint", userCheckpoint);
      this.$set(this, "userAmountOfDeposits", userAmountOfDeposits);
      this.$set(this, "maxInvestedUser", maxInvestedUser);
      this.$set(this, "bonusUsers", bonusUsers);
      this.$set(this, "marketingAddress", marketingAddress);
      this.$set(this, "projectAddress", projectAddress);
      this.$set(this, "userDividends", userDividends);
      this.$set(this, "userReferrer", userReferrer);
      this.$set(this, "isActive", isActive);
      let userLastInvestInfo = null;
      if(userAmountOfDeposits>0) userLastInvestInfo = await this.contract.methods.getUserDepositInfo(this.account,userAmountOfDeposits-1).call();
      this.$set(this, "userLastInvestInfo",userLastInvestInfo);
    }
  }
}
